import axios from 'axios';

import { LOGIN, LOGOUT } from '../actions/user';

const auth = (store) => (next) => (action) => {
  switch (action.type) {
    case LOGIN: {
      console.log('AUTH MIDDLEWARE: login passe');
      axios({
        method: 'post',
        url: 'http://localhost:5000/login',
        data: {
          email: store.getState().login.email,
          password: store.getState().login.password,
        },
      })
        .then((response) => {
          console.log('j affiche la page profil');
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      // je laisse passer tout de suite au middleware/reducer suivant
      next(action);
      break;
    }
    case LOGOUT:
      axios.post('http://localhost:5000/logout', {}, {
        withCredentials: true,
      });
      next(action);
      break;
    default:
      console.log('AUTH MIDDLEWARE: ', action, 'passe, je laisse paser');
      next(action);
  }
};

export default auth;
